/* src/App.css */

html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: hidden; /* Prevent overflow on the root elements */
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: black;
  overflow: hidden; /* Prevent overflow within the App */
}

.container {
  width: 800px;
  height: 600px;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  color: white; /* Color for BIOS text */
  font-family: monospace;
  transition: background-color 1s, color 1s, background-image 0.5s; /* Include background image transition */
  overflow: hidden; /* Prevent overflow within the container */
  position: relative; /* For positioning the setup message and logo */
}

.container.loading {
  background-color: rgba(211, 211, 211, 0.1); /* Lightgrey with 10% opacity */
}

.container.loaded {
  background-color: rgb(51, 102, 153); /* Updated background color */
  color: black; /* Change text color for final state if needed */
  justify-content: center; /* Center the login container vertically */
  align-items: center; /* Center the login container horizontally */
}

.container.show-background {
  background-image: url('/public/background.png'); /* Set background image */
  background-size: cover; /* Cover the entire container */
  background-position: center; /* Center the background image */
}

.container.intermediate {
  background-image: url('/public/background.png'); /* Set background image */
  background-size: cover; /* Cover the entire container */
  background-position: center; /* Center the background image */
  display: flex;
  justify-content: center;
  align-items: center;
}

.intermediate-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.intermediate-logo {
  width: 300px; /* Adjust the size as needed */
  height: auto;
}

.logo {
  position: absolute;
  top: 40px; /* Adjusted top position */
  right: 40px; /* Positioned to the right */
}

.logo img {
  width: 150px; /* Adjusted size */
  height: auto;
}

.bios-text {
  text-align: left;
  white-space: pre-wrap; /* Preserve whitespace formatting */
  line-height: 0.2; /* Reduce line height to print lines closer together */
  margin-top: 0; /* Ensure the text starts from the top */
}

.setup-message {
  position: absolute;
  bottom: 10px;
  left: 10px;
  font-size: 14px;
}

.login-container {
  width: 350px;
  background-color: #f0f0f0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.login-header {
  background: linear-gradient(to right, rgb(0, 0, 128), rgb(16, 132, 208)); /* Gradient background */
  color: white;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  font-weight: bold;
}

.close-button {
  cursor: pointer;
  padding: 0 10px;
}

.login-body {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.login-logo2 {
  width: 128px; /* Increased size */
  height: 64px; /* Increased size */
  margin-bottom: 20px;
  align-self: flex-start; /* Align to the left */
}

.login-fields {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.login-fields label {
  margin-bottom: 5px;
  font-size: 12px;
}

.user-select {
  margin-bottom: 10px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  background-color: white;
}

.user-option {
  padding: 5px;
}

.user-option.highlighted {
  background-color: #007bff;
  color: white;
}

.blank-space {
  height: 40px; /* Adjust height as needed */
  background-color: white;
}

.login-fields input {
  margin-bottom: 10px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 14px;
}

.login-buttons {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
}

.login-buttons button {
  margin-left: 10px;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  background-color: #007bff;
  color: white;
  font-size: 14px;
  cursor: pointer;
}

.login-buttons button:hover {
  background-color: #0056b3;
}

.main-content {
  flex: 1;
  width: 100%;
  overflow: hidden; /* Prevent overflow within the main content */
}

.taskbar {
  width: 100%;
  height: 40px;
  background-color: #c3c3c3; /* Taskbar background color */
  display: flex;
  justify-content: space-between; /* Space between start button and clock */
  align-items: center;
  position: absolute;
  bottom: 0;
}

.start-button {
  background-color: #c3c3c3; /* Same color as taskbar */
  color: black;
  border: 1px solid #808080; /* Dark gray border */
  box-shadow: inset 1px 1px 0px #fff, inset -1px -1px 0px #808080; /* Swapped box shadow */
  padding: 5px 10px;
  cursor: pointer;
  margin-left: 5px;
  display: flex;
  align-items: center;
}

.start-button img {
  width: 16px; /* Small size for the logo */
  height: 16px;
  margin-right: 5px; /* Space between the logo and the text */
}

.start-button:hover {
  background-color: #b3b3b3;
}

.start-menu {
  position: absolute;
  bottom: 40px; /* Position above the taskbar */
  left: 5px; /* Align with the start button */
  width: 250px; /* Adjusted width */
  background-color: #c0c0c0; /* Classic Windows 98 background color */
  border: 2px solid #000; /* Black border */
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); /* Subtle shadow for depth */
  z-index: 10;
  font-family: 'Tahoma', sans-serif; /* Classic Windows 98 font */
}

.start-menu-header {
  background-color: #000080; /* Dark blue header background */
  color: white;
  padding: 5px;
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: bold;
}

.start-menu-logo {
  width: 20px;
  height: 20px;
  margin-right: 5px; /* Space between the logo and the text */
}

.start-menu-list {
  list-style-type: none;
  padding: 5px;
  margin: 0;
}

.start-menu-list li {
  padding: 4px 8px;
  cursor: pointer;
  font-size: 12px;
}

.start-menu-list li:hover {
  background-color: #000080; /* Dark blue hover background */
  color: white;
}

.clock {
  color: black;
  font-size: 16px;
  padding: 0 10px;
  margin-right: 5px;
  background-color: #c3c3c3;
  border: 1px solid #808080;
  box-shadow: 1px 1px 0px #fff, -1px -1px 0px #000; /* Swapped box shadow */
}

.selection-box-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden; /* Prevent overflow within the container */
}

.selection-box {
  position: absolute;
  border: 1px dashed #1d1d1d; /* Updated border color */
  pointer-events: none; /* Ensure it doesn't interfere with mouse events */
}

.right-click-menu {
  position: absolute;
  background-color: #c0c0c0;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); /* Subtle shadow */
  z-index: 1000;
  width: 200px;
  font-family: 'Tahoma', sans-serif;
  border: 1px solid #808080;
}

.right-click-menu-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.right-click-menu-item {
  padding: 4px 8px;
  cursor: pointer;
  font-size: 12px;
  background-color: #c0c0c0;
  display: flex;
  justify-content: space-between;
  color: black; /* Default text color */
}

.right-click-menu-item.has-submenu {
  padding-right: 16px; /* Add padding to the right side for buffer zone */
}

.right-click-menu-item:hover {
  background-color: #000080;
  color: white; /* Hover text color */
}

.right-click-menu-item.disabled {
  color: gray;
  cursor: default;
}

.right-click-menu-item.disabled:hover {
  background-color: #c0c0c0;
  color: gray;
}

.right-click-submenu {
  position: absolute;
  top: 0;
  left: 100%;
  background-color: #c0c0c0;
  list-style-type: none;
  padding: 0;
  margin: 0;
  border: 1px solid #808080;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); /* Subtle shadow */
  width: 150px;
  display: none; /* Default hidden state */
}

.right-click-menu-item:hover > .right-click-submenu {
  display: block; /* Show submenu on hover */
}

.right-click-submenu .right-click-menu-item {
  color: black; /* Default text color for submenu items */
}

.right-click-submenu .right-click-menu-item:hover {
  background-color: #000080;
  color: white; /* Hover text color for submenu items */
}

.submenu-arrow {
  font-size: 10px;
}

.menu-separator {
  height: 1px;
  background-color: #808080;
  margin: 2px 0;
}

.desktop-icon {
  width: 100px; /* Maintain the width */
  text-align: center;
  margin: 10px 0; /* Adjust the margin to align vertically */
  cursor: pointer;
  user-select: none;
  position: absolute;
  transition: all 0.2s ease; /* Smooth transition for hover and selection effects */
}

.desktop-icon-image {
  width: 50px; /* Set the size to 50px */
  height: 50px; /* Set the size to 50px */
  margin-bottom: 10px; /* Add some space between the icon and text */
}

.desktop-icon-name {
  font-size: 14px; /* Increase the font size */
  color: black;
  display: block;
  margin-top: 5px;
}

.selected {
  opacity: 0.8; /* Slightly transparent for a soft highlight */
}

.selected .desktop-icon-image {
  filter: brightness(1.2); /* Slightly increase brightness for the icon */
}

.selected .desktop-icon-name {
  color: white; /* Keep text color white for contrast */
  background-color: rgba(0, 120, 215, 0.3); /* Transparent blue background for highlight */
  border-radius: 3px; /* Slightly rounded corners */
  padding: 2px 4px; /* Padding around the text */
  display: inline-block; /* Ensures the background covers only the text */
}